.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  .loading-image {
    margin-bottom: 10px;
  }
  
  .loading-image img {
    max-width: 200px;
    max-height: 200px;
  }
  
  .loading-spinner {
    border: 16px solid #f3f3f3;
    scale: 30%;
    border-top: 16px solid #3498db;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }